import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Box from '@material-ui/core/Box';
import Collapse from '@material-ui/core/Collapse';
import IconButton from '@material-ui/core/IconButton';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp';
import StatesAndDistrict from '../constants/states.json';

const useRowStyles = makeStyles({
  root: {
    '& > *': {
      borderBottom: 'unset',
    },
  },
});


function Row(props) {
  const { row } = props;
  const [open, setOpen] = React.useState(false);
  const classes = useRowStyles();
  var arr1=[],arr2=[]
  StatesAndDistrict.map(function(item){
    if(item.name===row.state){
      item.districts.map(item1=>{
         arr2.push(row.district[item1.name])
         arr1.push(item1.name)
      })
    }
  })
  return (
    <React.Fragment>
      <TableRow className={classes.root}>
        <TableCell>
          <IconButton aria-label="expand row" size="small" onClick={() => setOpen(!open)}>
            {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
          </IconButton>
        </TableCell>
        <TableCell component="th" scope="row">
          {row.state}
        </TableCell>
        <TableCell align="">{row.confirmed}</TableCell>
        <TableCell align="">{row.recovered}</TableCell>
        <TableCell align="">{row.deaths}</TableCell>
      </TableRow>
      <TableRow>
        <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={6}>
          <Collapse in={open} timeout="auto" unmountOnExit>
            <Box margin={1}>
              <Typography variant="h6" gutterBottom component="div">
                Distrcits
              </Typography>
              <Table size="small" aria-label="purchases">
                <TableHead>
                  <TableRow>
                    <TableCell>District</TableCell>
                    <TableCell>Confirmed</TableCell>
                    <TableCell align="">Recovered</TableCell>
                    <TableCell align="">Deceased</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {
                    arr1.map((distValue,i)=>(
                    <TableRow key={distValue}>
                    <TableCell component="th" scope="row">
                      {distValue}
                    </TableCell>
                    <TableCell>{arr2[i].confirmed}</TableCell>
                    <TableCell align="">{arr2[i].recovered}</TableCell>
                    <TableCell align="">
                      {arr2[i].deceased}
                    </TableCell>
                  </TableRow>
                  ))
                  }        
                </TableBody>
              </Table>
            </Box>
          </Collapse>
        </TableCell>
      </TableRow>
    </React.Fragment>
  );
}


class StateAndDistrictsTable extends React.Component {
  constructor(props){
    super(props)
    this.state={
      stateDetails:'',
      stateAPI:[]
    }
}
  componentDidMount(){
   this.setState({
    stateDetails:this.props.statewiseApi
   })
}
  render(){
    var json=this.state.stateDetails;
    var arr=[];
    Object.keys(json).forEach(function(key){
      arr.push(json[key])
    })
    
  return (
    <TableContainer component={Paper}>
      <Table aria-label="collapsible table">
        <TableHead>
          <TableRow>
            <TableCell />
            <TableCell>State</TableCell>
            <TableCell align="">Confirmed</TableCell>
            <TableCell align="">Recovered</TableCell>
            <TableCell align="">Deaths</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {arr.map((row) => (
            <Row key={row.state} row={row}/>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
          }
}

export default StateAndDistrictsTable;