import React from 'react';
import { Row,Col } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.css';
import StateList from  '../constants/states.json';
import IndiaFlag from '../images/tn_in-flag.png';
import Globe from '../images/globe.png';
import ProgressBar from '../components/ProgressBar';
import StateAndDistrictsTable from '../components/TableComponent';

class RootContainer extends React.Component{
    constructor(){
        super()
        this.state={
            global:'',
            countryTotal:'',
            stateWiseApi:'',
            selectState:'Select',
            stateDetails:''
        };
        this.stateChange = this.stateChange.bind(this);
    }

    stateChange=event=>{
        this.setState({
            selectState: event.target.value,
            stateDetails: this.state.stateWiseApi[event.target.value]
        })
    }
    componentDidMount(){
      var requestOptions = {
        method: 'GET',
        redirect: 'follow'
      };
      
      fetch("https://api.covid19api.com/summary", requestOptions)
      .then(response => response.json())
        .then(result => this.setState({ global : result.Global}))
        .catch(error => console.log('error', error));

    fetch("https://corona-virus-world-and-india-data.p.rapidapi.com/api_india", {
	"method": "GET",
	"headers": {
		"x-rapidapi-host": "corona-virus-world-and-india-data.p.rapidapi.com",
		"x-rapidapi-key": "ba0842b536msh2d03d2ced35ffb3p1f60e5jsn794d97687977"
	}})
    .then(response => response.json())
    .then(result => this.setState({ countryTotal: result.total_values, stateWiseApi: result.state_wise  }))
    .catch(err => {console.log(err)});
     }
    render(){
        console.log(this.state.stateWiseApi)
        let recPercent=0,descPercent=0;
        if(this.state.selectState==='Select'){
            recPercent= ((this.state.countryTotal.recovered/this.state.countryTotal.confirmed)*100).toFixed(2)
            descPercent= ((this.state.countryTotal.deaths/this.state.countryTotal.confirmed)*100).toFixed(2)
        }
        else{
            recPercent= ((this.state.stateDetails.recovered/this.state.stateDetails.confirmed)*100).toFixed(2)
            descPercent= ((this.state.stateDetails.deaths/this.state.stateDetails.confirmed)*100).toFixed(2)
        }
        const worldRecPerecent=((this.state.global.TotalRecovered/this.state.global.TotalConfirmed)*100).toFixed(2)
        const worldDescPercent=((this.state.global.TotalDeaths/this.state.global.TotalConfirmed)*100).toFixed(2)
        return(
            <React.Fragment>
                <div className='overview'>
                    <Row className="bgclr">
                        <Col md={{ span: 8, offset: 2 }} className="overviewDiv">
                            <div style={{left : '55px', width:'45%', position:'absolute'}}>
                            <Row style={{paddingTop: '30px'}}>
                              <Col md={6}>
                                <div style={{marginLeft:'43px'}}>   
                                <img src={IndiaFlag} alt="Indian Flag" className="iconDetails"/>
                                </div>
                                <div className="countryStyle">
                                <h2>India</h2>
                                </div>                
                              </Col>
                           </Row>
                           <Row>
                               <Col md={4} style={{padding: '3px 52px'}}>
                                   <select className="select"
                                        id="selectState"
                                        value={this.state.selectState}
                                        onChange={this.stateChange}
                                   >
                                       <option value="Select">Select States</option>
                                       {StateList.map(state=>(
                                           <option
                                            key={state.code}
                                            value={state.name}
                                           >   
                                            {state.name}
                                           </option>
                                       ))}
                                   </select>
                               </Col>
                           </Row>
                           <div className="casesFont" style={{padding: '16px 44px'}}>
                               {
                                (this.state.selectState ==='Select') ?
                            <Row> 
                                <Col>
                                <p>Confirmed</p>
                                <p className="confirmedDigit">{this.state.countryTotal.confirmed}</p>
                                </Col>
                                <Col>
                                <p>Recovered</p>
                                <p className="recoveredDigit">{this.state.countryTotal.recovered}</p>
                                </Col>
                                <Col>
                                <p>Deceased</p>
                                <p>{this.state.countryTotal.deaths}</p>
                                </Col> 
                            </Row> : 
                            <Row> 
                                <Col>
                                <p>Confirmed</p>
                                <p className="confirmedDigit">{this.state.stateDetails.confirmed}</p>
                                </Col>
                                <Col>
                                <p>Recovered</p>
                                <p className="recoveredDigit">{this.state.stateDetails.recovered}</p>
                                </Col>
                                <Col>
                                <p>Deceased</p>
                                <p>{this.state.stateDetails.deaths}</p>
                                </Col> 
                            </Row>
                              }
                            </div>
                            <div style={{width:'90%'}}>
                            <Row>
                              <Col md={6}>
                              <ProgressBar percent={recPercent} text={'Recovered'}/>
                              </Col>
                              <Col md={6}>
                              <ProgressBar percent={descPercent} text={'Deceased'}/>
                              </Col>
                            </Row>  
                            </div>
                           </div>
                           <div className="verticalLine"></div>
                           <div style={{right:'0px', width:'45%',position:'absolute'}}>
                           <Row>
                              <Col md={8} className="worldStyle">
                                  <div>
                                  <img src={Globe} alt="Worldwide" className="iconDetails"/>
                                  </div>
                                  <div style={{padding:'6px'}}>
                                    <h3>Worldwide</h3>
                                  </div>                              
                              </Col>
                            </Row>
                            <div className="casesFont" style={{paddingLeft:'10px',paddingRight:'90px',marginTop:'18px'}}>
                            <Row>
                               <Col>
                                <p>Confirmed</p>
                                <p className="confirmedDigit">{this.state.global.TotalConfirmed}</p>
                                <p><span id="upwards" className="confirmedDigit">&#8593;</span></p>
                                <p id="digitSize" className="confirmedDigit">
                                    {this.state.global.NewConfirmed}
                                </p>
                               </Col>
                               <Col>
                                <p>Recovered</p>
                                <p className="recoveredDigit">{this.state.global.TotalRecovered}</p>
                                <p><span id="upwards" className="recoveredDigit">&#8593;</span></p>
                                <p id="digitSize" className="recoveredDigit">{this.state.global.NewRecovered}</p>
                               </Col>
                               <Col>
                                <p>Deceased</p>
                                <p> {this.state.global.TotalDeaths}</p>
                                <p><span id="upwards">&#8593;</span></p>
                                <p id="digitSize">{this.state.global.NewDeaths}</p>
                               </Col>
                           </Row>
                           </div>
                           <div style={{width:'90%', marginTop:'18px'}}>
                            <Row>
                              <Col md={6}>
                              <ProgressBar percent={worldRecPerecent} text={'Recovered'}/>
                              </Col>
                              <Col md={6}>
                              <ProgressBar percent={worldDescPercent} text={'Deceased'}/>
                              </Col>
                            </Row>  
                            </div>
                           </div>
                        </Col>
                    </Row>
                </div>
                <div>
                    <div className='tableheading'>
                        <br></br>
                        <p id="tabletext">All States/ Union Territories</p>   
                        <br></br>  
                    </div>
                </div>
                {this.state.stateWiseApi !==''?
                <div className="tabledata">
                    <StateAndDistrictsTable statewiseApi={this.state.stateWiseApi}/>
                </div> : ''}
                <br></br>
                <br></br>
                <br></br>
                <div className="footer">
                    <br></br>
                    <p></p>
                    <br></br>
                </div>
            </React.Fragment>   
        );
    }
}

export default RootContainer;